.header {
  width: 100%;
  height: auto;
  display: grid;
  grid-template-rows: repeat(3, auto);
  color: #333;
}

.header__topbar {
  width: 100%;
  height: 30px;
  background-color: #ffdede;
  line-height: 1.9;
  display: flex;
  justify-content: center;
}

.header__topbar a {
  padding: 0px 10px;
  color: black;
}

.header__title {
  width: 100%;
}

.header__title a {
  text-decoration: none;
  color: #333;
}

.header__title h1 {
  text-align: center;
  font-size: 60px;
}

.header__navigation {
  width: 100%;
  height: 60px;
  border-top: 2px solid #ffdede;
  border-bottom: 2px solid #ffdede;
  display: flex;
  justify-content: center;
}

.header__navigation a {
  color: #333;
  text-decoration: none;
}

.header__navigation ul {
  list-style-type: none;
  display: flex;
  -webkit-padding-start: 0px;
          padding-inline-start: 0px;
  line-height: 1.7;
}

.header__navigation li {
  margin: 0px 10px;
}

.header__navigation li:hover {
  background-color: #ffdede;
  cursor: pointer;
}

@media only screen and (max-width: 600px) {
  .header__title h1 code {
    font-size: 35px;
  }
}
.aboutme {
    display: flex;
    flex-direction: column;
    text-align: center;
}

.aboutme img {
    width: 300px;
    height: 300px;
    border-radius: 50%;
    margin-bottom: 20px;
    align-self: center; 
}

.aboutme span {
    width: 100%;
    height: 30px;
    background-color: #ffdede;
}

.aboutme article {
    margin: 30px 0px;
}
.medellincss {
  width: 100%;
  text-align: center;
}

.medellincss img {
  width: 80%;
  margin: 20px;
  border: 5px solid #fbfad3;
}

.medellincss__images {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
}
.card {
  width: 100%;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  -webkit-transition: 0.3s;
  transition: 0.3s;
  border-radius: 5px;
  display: grid;
  grid-template-rows: auto auto;
}

.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

.card__img {
  width: 100%;
  height: 280px;
  border-radius: 5px 5px 0 0;
}

.card__text {
  background: #fbfad3;
  padding: 20px;
  border-radius: 0 0 5px 5px;
  display: grid;
  grid-template-columns: 30px auto;
  text-align: left;
}

.card__text a {
  cursor: pointer;
  color: #333;
}
.workshops {
  width: 100%;
  text-align: center;
}

.workshops__cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  grid-gap: 20px;
  padding: 30px 20px;
}
.talks {
    width: 100%;
    text-align: center;
}

.talks__cards {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    grid-gap: 20px;
    padding: 30px 20px;
}
.app {
    display: flex;
    justify-content: center;
    margin-top: 30px;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
  line-height: 1.6;
}

span {
  background-color: #ffdede;
  width: 100%;
  height: 30px;
}

img {
  object-fit: cover;
}

@media only screen and (max-width: 600px) {
  code {
    font-size: 12px;
  }
}
